import React from "react";
import { useGetLocale } from "providers";

import "config/themes/_wager.scss";

import icon_url from "./content/coin_icon.png";
import css from "./style.module.scss";

const Header = () => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <div className={css.imgWrapper}>
        <img src={icon_url} alt="icon" className={css.img} />
      </div>

      <div className={css.textWrapper}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "winBonuses.title" })}
        </h1>

        <p className={css.text}>
          {intl.formatMessage({ id: "winBonuses.main" })}
        </p>
      </div>
    </div>
  );
};

export default Header;
