import React, { FC } from "react";
import { useGetLocale } from "providers";

import { Record } from "pages/WinBonuses/types";
import { BonusesTable } from "./Table";
import css from "./style.module.scss";

interface Props {
  records: Record[] | null;
}

const Table: FC<Props> = ({ records }) => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <h1 className={css.title}>
        {intl.formatMessage({ id: "winBonuses.table.title" })}
      </h1>
      <div className={css.tableWrapper}>
        {records ? (
          <BonusesTable data={records} />
        ) : (
          <p className={css.text}>
            {intl.formatMessage({ id: "winBonuses.table.emptyTable" })}
          </p>
        )}
      </div>
    </div>
  );
};

export default Table;
