import React, { FC } from "react";
import { useGetLocale } from "providers";

import { Record } from "pages/WinBonuses/types";

import Graph from "./components/Graph";
import ShowValue from "../ShowValue";
import css from "./style.module.scss";

interface Props {
  data: {
    value: string;
    coin: string;
  };
  date?: string;
  graphData: Record[] | null;
}

const GraphPage: FC<Props> = ({ data, date, graphData }) => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <div className={css.overlay} />
      <div>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "winBonuses.right.title" })}
        </h1>
      </div>

      <div className={css.valueBox}>
        <ShowValue
          data={data}
          text={intl.formatMessage({ id: "winBonuses.right.text" })}
          date={date}
        />
      </div>

      <div className={css.graphBox}>
        {graphData && <Graph data={graphData} />}
      </div>
    </div>
  );
};

export default GraphPage;
