import React, { FC } from "react";
import { useGetLocale } from "providers";

import { Table } from "components";

import { Record } from "pages/WinBonuses/types";

import css from "./style.module.scss";
import { getIntlValue } from "providers";
interface Props {
  data: Record[];
}

const CellWrapper: FC = ({ children }) => (
  <div className={css.cellWrapper}>{children}</div>
);

export const BonusesTable = ({ data }: Props) => {
  const intl = useGetLocale();

  const formatMessage = getIntlValue(intl, "winBonuses.table.header.");

  return (
    <div className={css.root}>
      <div className={css.tableWrapper}>
        <Table
          head={{
            values: [
              formatMessage("datetime"),
              formatMessage("credited"),
              formatMessage("total"),
            ],
            classes: {
              th: css.th,
            },
          }}
          body={{
            rows: data?.map((record) => {
              const { credited_fey, total_wagered_usd, datetime } = record;

              return {
                values: [
                  datetime,
                  credited_fey + " FEY",
                  total_wagered_usd + " USD",
                ],
                key: datetime,
              };
            }),

            classes: {
              td: css.td,
            },
            components: {
              cell: CellWrapper,
            },
          }}
        />
      </div>
    </div>
  );
};
