import React, { FC, useRef } from "react";

import css from "./style.module.scss";

interface Props {
  data: {
    value: string;
    coin: string;
  };
  title?: string;
  date?: string;
  text: string;
}

const ShowValue: FC<Props> = ({ data: { coin, value }, title, text, date }) => {
  const contentRef = useRef<HTMLDivElement>(null);

  return (
    <div className={css.root}>
      <div className={css.contentBox} ref={contentRef}>
        <div className={css.overlay} />
        <div className={css.textWrapper}>
          <h2 className={css.title}>{title}</h2>

          <p className={css.value}>
            {`${value} `}
            <span className={css.coin}>{coin}</span>
          </p>

          <h2 className={css.title}>{date}</h2>
        </div>
      </div>

      <p className={css.dataText}>{text}</p>
    </div>
  );
};

export default ShowValue;
