import React, { useEffect, useRef, useState } from "react";
import { useGetLocale } from "providers";
import { Helmet } from "react-helmet";

import { useFetch, useLocalStorage } from "hooks";

import ShowValue from "./components/ShowValue";
import Header from "./components/Header";
import Graph from "./components/Graph";
import Table from "./components/Tab";

import { Record, RecordsValues } from "./types";
import css from "./style.module.scss";

const WinBonuses = () => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const blockRef = useRef<HTMLDivElement | null>(null);

  const [value, setValue] = useState({
    pending: {
      value: (0).toFixed(8),
      coin: "FEY",
    },
    credited: {
      value: (0).toFixed(8),
      coin: "FEY",
    },
  });

  const [records, setRecords] = useState<Record[] | null>(null);

  const [bonusesFetch, { data: bonusesData }] = useFetch<RecordsValues>(
    "/statistics/wager-mining",
  );

  useEffect(() => {
    if (token) {
      bonusesFetch();
    }
  }, [token]);

  useEffect(() => {
    if (bonusesData) {
      const { records, credited_fey, pending_fey } = bonusesData;

      setRecords(records);

      setValue({
        pending: {
          ...value.pending,
          value: pending_fey,
        },
        credited: {
          ...value.credited,
          value: credited_fey,
        },
      });
    }
  }, [bonusesData]);

  return (
    <>
      <Helmet>
        {/* <!-- Primary Meta Tags --> */}
        <title>Pasino - Feyorra Wagermining</title>
        <meta name="title" content="Pasino - Feyorra Wagermining" />
        <meta
          name="description"
          content="Feyorra will distribute 40%
of the initial supply via Wager Mining. Users are rewarded by
placing bets on Pasino.com"
        />
        {/* <!-- Open Graph / Facebook --> */}
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://pasino.com/wager-mining" />
        <meta
          property="og:title"
          content="Pasino - Feyorra
Wagermining"
        />
        <meta
          property="og:description"
          content="Feyorra will
distribute 40% of the initial supply via Wager Mining. Users
are rewarded by placing bets on Pasino.com"
        />
        <meta property="og:image" content="" />
        {/* <!-- Twitter --> */}
        <meta property="twitter:card" content="summary_large_image" />
        <meta
          property="twitter:url"
          content="https://pasino.com/wager-mining"
        />
        <meta
          property="twitter:title"
          content="Pasino - Feyorra
Wagermining"
        />
        <meta
          property="twitter:description"
          content="Feyorra will
distribute 40% of the initial supply via Wager Mining. Users
are rewarded by placing bets on Pasino.com"
        />
        <meta property="twitter:image" content="" />
      </Helmet>

      <div className={css.wrapper} ref={blockRef}>
        <Header />

        <div className={css.box}>
          <div className={css.pendingBox}>
            <ShowValue
              data={value.pending}
              title={intl.formatMessage({ id: "winBonuses.left.title" })}
              text={intl.formatMessage({ id: "winBonuses.left.text" })}
            />
          </div>

          <Graph data={value.credited} graphData={records} />

          <Table records={records} />
        </div>
      </div>
    </>
  );
};

export default React.memo(WinBonuses);
