import React, { FC, useEffect, useRef } from "react";

import { chartJS } from "utils/ChartJS";
import type { Chart } from "chart.js";

import { Record } from "pages/WinBonuses/types";
import { setConfig } from "./chartConfig";
import css from "./style.module.scss";

interface Props {
  data: Record[];
}

const dateReg = /(\d{4})-(\d{2})/g;

const Graph: FC<Props> = ({ data }) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const chartRef = useRef<Chart>();

  const graphData = data.map((value) => {
    let newDate = value.datetime;

    const regDate = value.datetime.match(dateReg);
    if (regDate) {
      newDate = regDate[0];
    }

    return {
      data: Number(value.credited_fey),
      date: newDate,
    };
  });

  useEffect(() => {
    if (canvasRef.current) {
      chartJS.get((Chart) => {
        if (canvasRef.current)
          chartRef.current = new Chart(canvasRef.current, setConfig(graphData));
      });
    }
  }, [canvasRef]);

  useEffect(() => {
    if (chartRef.current) {
      chartRef.current.height = 300;
    }
  }, [chartRef]);

  return (
    <div className={css.root}>
      <canvas
        width="100%"
        height="300px"
        ref={canvasRef}
        className={css.canvas}
      />
    </div>
  );
};

export default Graph;
