interface ConfigSettings {
  data: number;
  date: string;
}

export const setConfig = (content: ConfigSettings[]): any => {
  const data = content.map(({ data }) => data);
  const date = content.map(({ date }) => date);

  if (data[0] !== 0) {
    data.unshift(0);
    date.unshift("");
  }

  return {
    type: "line",
    data: {
      labels: [...date],
      datasets: [
        {
          data: [...data],
          borderColor: "#736b82",
          backgroundColor: "transparent",
          pointBackgroundColor: "#241a3d",
          borderWidth: 2,
          pointRadius: 4,
          label: "",
        },
      ],
    },
    options: {
      maintainAspectRatio: false,
      layout: {
        padding: 5,
      },
      hover: {
        mode: "index",
      },
      responsive: true,

      elements: {
        line: {
          tension: 0,
        },
      },

      legend: {
        display: false,
      },

      tooltips: {
        caretSize: 0,
        titleFontColor: "transparent",
        titleFontSize: 0,
        displayColors: false,
        callbacks: {
          label: (item: any, data: any) => {
            return item.value || "";
          },
        },
      },

      scales: {
        xAxes: [
          {
            ticks: {
              autoSkip: true,
              maxRotation: 90,
              minRotation: 90,
            },
            gridLines: {
              display: false,
            },
          },
        ],

        yAxes: [
          {
            ticks: {
              beginAtZero: true,
            },
            gridLines: {
              display: false,
            },
          },
        ],
      },
    },
  };
};
